

/* BASE - Base style, mixins, variables, etc */
@import 'base/variables';
@import 'base/base';
@import 'base/typos';

/* LAYOUTS */
@import 'layouts/header';
@import 'layouts/footer';
@import 'layouts/sections';

/* OTHER MODULES */
@import 'modules/helpers';
@import 'modules/elements';
@import 'modules/buttons';
@import 'modules/responsive';
@import 'modules/skills';
@import 'modules/servicebox';
@import 'modules/clients';
@import 'modules/slick';
@import 'modules/contact';


$fontstack: 'Poppins', sans-serif;
$fontbody: 'Open Sans', sans-serif;
$colordefault: #000;
$colorblue: #4A63E7;
$colororange: #FF5959;

@mixin transition($second) {
    -webkit-transition: all $second ease-in-out;
      -moz-transition: all $second ease-in-out;
          transition: all $second ease-in-out;
  }



  .pagination {
    display: inline-block;
    padding-left: 0;
    margin: 0;
    border-radius: 4px;
}

.pagination > li:first-child > a,.pagination > li:first-child > span {
    margin-left: 0;
    border-radius: 50%;
}

.pagination > li:last-child > a,.pagination > li:last-child > span {
    border-radius: 50%;
}

.pagination > .active > a,.pagination > .active > a:focus,.pagination > .active > a:hover,.pagination > .active > span,.pagination > .active > span:focus,.pagination > .active > span:hover {
    z-index: 3;
    color: #fff;
    cursor: default;
    background-color: $colordefault;
    border-color: transparent;
}

.pagination > li > a,.pagination > li > span {
    position: relative;
    float: left;
    font-weight: 400;
    height: 46px;
    line-height: 32px !important;
    margin-left: 11px;
    line-height: 1.42857143;
    color: #1a1a1a;
    text-decoration: none;
    background-color: #fff;
    border: 1px solid #EEE;
    border-radius: 50%;
    width: 46px;
    text-align: center;
    @include transition(.3s);
}

.pagination > li > a:focus,.pagination > li > a:hover,.pagination > li > span:focus,.pagination > li > span:hover {
    z-index: 2;
    color: #FFF;
    background-color: $colordefault;
    border-color: transparent;
}

.portfolio-wrapper {
	[className*="col-"] {
		margin-top: 10px;
		margin-bottom: 10px;
	}
}

.pf-filter-wrapper {
	display: none;
}

.portfolio-item {
	position: relative;
	overflow: hidden;
	border: 1px solid gray;
	border-radius: 10px;
	@include transition(.3s);
	.thumb {
		overflow: hidden;
		img {
			-webkit-transform: scale(1);
		    -moz-transform: scale(1);
		    -ms-transform: scale(1);
		    -o-transform: scale(1);
		    transform: scale(1);
		    @include transition(.3s);
		}
	}

	.details {
		color: #FFF;
		position: absolute;
		padding: 0 25px;
		top: 25px;
		width: 100%;
		z-index: 1;
		h4 {
			color: #FFF;
			margin: 0 0 4px;
			opacity: 0;
			transform: translateY(30px);
    		transition: all cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
		}
		span {
			font-size: 12px;
			opacity: 0;
			display: block;
			transform: translateY(40px);
    		transition: all cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
		}
	}
	.whlink{
		color:#1a1a1a;

	}
	.plus-icon {
		color: $colororange;
		background: #FFF;
		border-radius: 100%;
		position: absolute;
		font-size: 16px;
		left: 25px;
		bottom: 25px;
		opacity: 0;
		line-height: 32px;
		text-align: center;
		height: 30px;
		width: 30px;
		z-index: 1;
		@include transition(.3s);
	}
	.mask {
		background: $colororange;
		position: absolute;
		left: 0;
		top: 0;
		opacity: 0;
		height: 100%;
		width: 100%;
		@include transition(.3s);
	}
	&:hover {
		-webkit-box-shadow: 0px 6px 15px 0px rgba(0,0,0,0.15);
		-moz-box-shadow: 0px 6px 15px 0px rgba(0,0,0,0.15);
		box-shadow: 0px 6px 15px 0px rgba(0,0,0,0.15);
		.mask {
			opacity: .9;
		}
		img {
			-webkit-transform: scale(1);
		    -moz-transform: scale(1);
		    -ms-transform: scale(1);
		    -o-transform: scale(1);
		    transform: scale(1.1);
		}
		.details {
			h4, span {
			    opacity: 1;
    			transform: translateY(0);
			}
		}
		.plus-icon {
			opacity: 1;
		}
	}
}

.load-more {
	i {
		display: none;
	}
}

.portfolio-info {
    color: #888;
    font-size: 13px;
}

.portfolio-info li:not(:last-child) {
    margin-right: 1.5rem;
}

.portfolio-info i {
    color: $colororange;
    font-size: 14px;
    margin-right: 10px;
}

.portfolio-filter {
	margin-bottom: 25px;
	li {
		color: #888;
		cursor: pointer;
		font-size: 14px;
		font-weight: 400;
		position: relative;
		&:hover {
			color: #000;
			&:after {
				height: 7px;
			}
		}
		&.current {
			background: $colororange;
			color: #FFF;
			padding: 6px 14px 4px;
    		border-radius: 30px;
		}
	}
	li:not(:last-child) {
	    margin-right: 1.5rem;
	}
}

.price-item {
	border: solid 1px #EFEFEF;
	border-radius: 10px;
	padding: 30px 0;
	i {
		background: #FFF;
		font-size: 24px;
		color: $colororange;
		display: block;
		border-radius: 50%;
		border: solid 1px #EFEFEF;
		height: 55px;
		line-height: 55px;
		left: 50%;
		top: -27.5px;
		position: absolute;
		text-align: center;
		width: 55px;
		-webkit-transform: translateX(-50%);
	    -moz-transform: translateX(-50%);
	    -ms-transform: translateX(-50%);
	    -o-transform: translateX(-50%);
	    transform: translateX(-50%);
	}
	.price-icon + .plan {
		margin-top: 25px;
	}
	.plan {
		font-size: 12px;
		color: #FFF;
		background: $colororange;
		border-radius: 25px;
	    display: inline-block;
    	padding: 1px 9px;
	}
	.price {
		font-size: 36px;
		margin: 25px 0;
		em {
			font-size: 18px;
			font-style: normal;
		    vertical-align: super;
		    margin-right: 4px;
		}
		span {
			color: #888;
			font-size: 12px;
			font-weight: 400;
		}
	}
	ul {
		list-style: none;
		padding-left: 0;
		margin: -8px 0;
		li {
			line-height: 30px;
		}
	}
	.btn {
		margin-top: 30px;
	}
	&.recommended {
		color: #FFF;
		background: $colorblue;
		.price {
			color: #FFF;
			span {
				color: #FFF;
			}
		}
	}
}

.testimonial-item {
	border: solid 1px #EFEFEF;
	border-radius: 10px;
	padding: 30px 25px;
	.symbol {
		font-weight: bold;
		color: $colororange;
		font-size: 12px;
	    margin-bottom: 20px;
	    display: block;
	}
	p {
		margin-bottom: 0;
	}
	.testimonial-details {
		margin-top: 25px;
		.thumb {
			display: inline-block;
			border-radius: 100%;
			overflow: hidden;
			height: 40px;
		    vertical-align: middle;
			width: 40px;
		}
		.info {
			display: inline-block;
			margin-left: 20px;
		    vertical-align: middle;
			h4 {
				margin: 0;
			}
			span {
				font-size: 11px;
				text-transform: uppercase;
				letter-spacing: 0.4px;
			}
		}
	}
}
